import React from 'react'
import FileIcon from './fileIcon'

const FileList = ({files}) => <div className="file-list">
  <div className="file-list-wrapper">{files.map(({type, name}, index) => <div key={index} className="file">
    <FileIcon icon={type} /><span>{name}</span>
  </div>)}</div>
</div>

export default FileList


