import React from 'react'
import ReactAnimationFrame from 'react-animation-frame'
import FileIcon from '../fileIcon'
// import TimeLineElement from 'timeLineElement'
// import TimeLineEvent from 'timeLineEvent'

const EditorTools = () => <div className="editor-tools">
  <i className="square-icon vicon vicon-clock darker"></i>
  <h1>Editor Tools</h1>
  <i className="square-icon vicon vicon-smile"></i>
  <i className="square-icon vicon vicon-menu"></i>
  <i className="square-icon vicon vicon-magic-wand"></i>
  <i className="square-icon vicon vicon-move"></i>
  <i className="square-icon vicon vicon-full-screen"></i>
  <i className="square-icon vicon vicon-cut"></i>
  <i className="square-icon vicon vicon-bin"></i>
  <span className="spacer"></span>
  <a className="button inactive">Save draft</a>
  <a className="button">Render Composition</a>
</div>

const TimeLineElementVisibility = () => <i className="vicon vicon-visible"></i>

const TimeLineElement = ({ timeline }) => <div className="time-line-element">
  <FileIcon icon={timeline.file.type} />
  <span>{timeline.file.name}</span>
  <div className="icons">
    {timeline.file.type === 'video' ? <TimeLineElementVisibility /> : null}
    <i className="vicon vicon-audio-on"></i>
  </div>
</div>

const TimeLineEvent = ({ timeline }) => <div className="time-line-event">
  <div className="time-line-bar" style={{left: timeline.position * 10 + 'px', width: timeline.length * 10 + 'px'}}>
    <span class="left">𝙸 6;25</span>
    <span className="right">7:55 𝙸</span>
  </div>
</div>

const TimeLineTop = ({ style }) => <div className="time-line-top" style={style}>
</div>

class TimeLine extends React.Component {
  eventsContainer = null
  elementsContainer = null
  scrolling = 'elements'

  refEventsContainer = eventsContainer => {
    this.eventsContainer = eventsContainer
    eventsContainer.addEventListener('scroll', this.scrollTogether.bind(this, 'events'), true)
  }
  refElementsContainer = elementsContainer => {
    this.elementsContainer = elementsContainer
    elementsContainer.addEventListener('scroll', this.scrollTogether.bind(this, 'elements'), true)
  }

  scrollTogether(scrolling) {
    this.scrolling = scrolling
  }

  onAnimationFrame() {
    if (this.eventsContainer) {
      if (this.scrolling === 'elements') {
        this.eventsContainer.scrollTop = this.elementsContainer.scrollTop
      } else {
        this.elementsContainer.scrollTop = this.eventsContainer.scrollTop
      }
    }
  }

  render () {
    const containerWidth = `${this.props.videoLength * 10}px`
    return <div className="time-line">
      <div className="wrapper">
        <div className="time-line-container">
          <div className="time-line-elements-container"
               ref={this.refElementsContainer}>
            {this.props.timelines.map((timeline, index) => <TimeLineElement key={index} timeline={timeline}/>)}
          </div>
          <div className="time-line-right">
            <EditorTools/>
            <div className="time-line-scrollable-x">
              <TimeLineTop style={{ width: containerWidth }}/>
              <div className="time-pointer">
                <div className="position">0:01</div>
              </div>
              <div
                className="time-line-events-container"
                style={{ width: containerWidth }}
                ref={this.refEventsContainer}>
                {this.props.timelines.map((timeline, index) => <TimeLineEvent key={index} timeline={timeline}/>)}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  }
}

export default ReactAnimationFrame(TimeLine)


