import React from 'react'
import ReactPlayer from 'react-player'
import cn from 'classnames'

const forwardSpeed = 4
const backwardSpeed = -4

class Video extends React.Component {

  constructor () {
    super()
    this.state = {
      playing: false,
      played: 0,
      playedSeconds: 0,
      playbackRate: 1,
      loaded: 0
    }
  }

  play = () => {
    this.setState({
      playing: true,
      playbackRate: 1
    })
  }

  pause = () => {
    this.setState({
      playing: false
    })
  }

  refPlayer = player => {
    this.player = player
  }

  onProgress = (state) => {
    this.setState({
      played: state.played,
      playedSeconds: state.playedSeconds
    })
  }

  goBackwards = () => {
    this.setState({
      playbackRate: this.state.playbackRate === backwardSpeed
        ? 1
        : backwardSpeed
    })
  }

  goForward = () => {
    this.setState({
      playbackRate: this.state.playbackRate === forwardSpeed
        ? 1
        : forwardSpeed
    })
  }

  goPrevious = () => {
    if (this.player) {
      this.player.seekTo(0)
    }
  }

  goNext = () => {

  }

  render () {
    return <div className="video-wrapper">
      <h1>Preview</h1>
      <div className="video-box">
        <div className="video-box-header">
          <i className="vicon vicon-edit"></i>
          <input type="text" value="Name of your Project" className="project-name"/>
        </div>
        <div className="player">
          {this.state.playedSeconds === 0 && this.state.playing === false
            ? <i className="logo"/>
            : (<ReactPlayer url={this.props.url}
                            ref={this.refPlayer}
                            style={{
                              position: 'absolute',
                              left: 0,
                              top: 0,
                              bottom: 0,
                              right: 0
                            }}
                            width="100%"
                            height="100%"
                            playing={this.state.playing}
                            playbackRate={this.state.playbackRate}
                            onProgress={this.onProgress}
            />)
          }
        </div>
        <div className="video-box-controls">
          <i className="vicon vicon-previous" onClick={this.goPrevious}></i>
          <i className={cn(
            "vicon vicon-backward",
            {
              active: this.state.playbackRate === backwardSpeed
            }
          )} onClick={this.goBackwards}></i>
          <i className={cn(
            "vicon vicon-play",
            {
              active: this.state.playing
            }
          )} onClick={this.play}></i>
          <i className={cn(
            "vicon vicon-pause",
            {
              active: !this.state.playing && this.state.playedSeconds > 0
            }
          )} onClick={this.pause}></i>
          <i className={cn(
            "vicon vicon-forward",
            {
              active: this.state.playbackRate === forwardSpeed
            }
          )} onClick={this.goForward}></i>
          <i className="vicon vicon-next" onClick={this.goNext}></i>
        </div>
      </div>
    </div>
  }
}

export default Video


