import React, { Component } from 'react';
import './App.css';
import TimeLine from './components/timeLine'
import FileDraggable from './components/fileDraggable'
import FileList from './components/fileList'
import Video from './components/video'

const names = [
  'bromium',
  'peregrinationes',
  'omnes',
  'elogiumes',
  'resuscitabo',
  'nobilis',
  'superbus',
  'lixaes',
  'ecce',
  'fatalis',
  'heuretes'
]

const types = [
  'video',
  'jpg',
  'svg',
  'png',
  'mp3'
]

const videoLength = 380

const files = new Array(30).fill(0).map(() => {
  const type = types[Math.floor(Math.random()* types.length)]
  return {
  name: `${names[ Math.floor(Math.random() * names.length) ]}${type !== 'video' ? `.${type}` : ''}`,
    type
  }
})
const timeLines = new Array(50).fill(0).map(() => {
  return {
    file: files[Math.floor(Math.random()* files.length)],
    position: Math.floor(Math.random()* (videoLength - 50)),
    length: Math.floor(Math.random()* 30 + 20)
  }
})
const url = "http://commondatastorage.googleapis.com/gtv-videos-bucket/sample/BigBuckBunny.mp4"

class App extends Component {
  render() {
    return (
      <div className="App">
        <div className="project">
          <div className="wrapper">
            <div className="left-bar">
              <h1>Start your project</h1>
              <FileDraggable/>
              <FileList files={files}/>
            </div>
            <Video url={url}/>
          </div>
        </div>
        <TimeLine videoLength={videoLength} timelines={timeLines}/>
      </div>
    );
  }
}

export default App;



